import { Component } from '@angular/core';
import { IonicPage, NavController, LoadingController, AlertController, NavParams } from 'ionic-angular';
import { GlobalVarsProvider } from '../../providers/global-vars/global-vars';
import { AlertServiceProvider } from '../../providers/alert-service/alert-service';
import { ClientdataProvider } from '../../providers/clientdata/clientdata'

@IonicPage()
@Component({
  selector: 'page-balance',
  templateUrl: 'balance.html',
})
export class BalancePage {
  account: string;
  accounts: any = [];
  accounts_no: any = [];
  langs: any;
  response:  boolean = false;
  type: any;
  confirmReq: any;
  page: number =1;
  error: number =1;
  title: any;
  mydata: any;
  actualBalance: string ="";
  availableBalance: string = "";
  stat_title: string = "STATEMENTS";
  datetime: string;
  myDate: string = new Date().toString();
  unmasked_accounts: any = [];
  objectblock: any = {
    username: this.globalVars.getUsername(), account_from: '', lang: 'en', type: 'MWALLET'
  };
  constructor(public navCtrl: NavController, private clientdata: ClientdataProvider, public globalVars: GlobalVarsProvider, public navParams: NavParams,
    public loadingController: LoadingController, public alertService: AlertServiceProvider, public alertCtrl: AlertController) {

    this.langs = this.globalVars.doTranslate( this.globalVars.getLang() );
    this.objectblock.username = this.globalVars.Username;
    this.accounts = globalVars.linked_acc;
    this.objectblock.account_from = this.accounts[ 0 ].acc_number;
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad BalancePage');
  }


  openPage( page ) { 
    this.navCtrl.push( page );
  }
  goHome() {
    this.navCtrl.setRoot( 'MainPage' );
  }
  ionViewDidEnter() {
    this.globalVars.reload();

  }
  submit() {
    if (this.objectblock.account_from === null || this.objectblock.account_from === '' ||
      this.objectblock.account_from === undefined) {
      this.alertService.errorPop( this.langs.valid_acc);

    } else {
      this.objectblock.account_from = this.objectblock.account_from + "";
      if(this.objectblock.account_from.substring(0, 3) == "255"){
        this.objectblock.type='MWALLET';
      }else{
        this.objectblock.type='CBS';
      }
      console.log(this.objectblock);
      this.alertService.showLoading( this.langs.submitting_txt );
      this.clientdata.balance(this.objectblock)
        .subscribe( data => {
          data = this.globalVars.testdec( data );
          console.log( data )
          if (data.length != 0) {
            this.alertService.dismiss();
            if (!data.error) {
              this.actualBalance = data.actual;
              this.availableBalance = data.available;
              this.datetime = new Date().toLocaleString();
              this.response = true;
            } else if (data.error) {
              this.alertService.errorPop( data.message );
              this.navCtrl.pop();
            } else {
              this.alertService.errorPop( this.langs.tech );
              this.navCtrl.pop();
            }
          } else {
            this.alertService.dismiss();
            this.alertService.errorPop( this.langs.tech );
            this.navCtrl.pop();
          }
        }, err => {
          console.log( err );
          this.alertService.dismiss();
          this.alertService.errorPop( this.langs.tech );
        });
    }

  }

  
  

}



